import React, { useRef } from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import { environment } from "../../environments/environment";
import { VscSave } from "react-icons/vsc";
import "primeicons/primeicons.css";
import "primereact/resources/themes/lara-light-indigo/theme.css";
import "primereact/resources/primereact.css";
import "primeflex/primeflex.css";
import DownloadForOfflineRoundedIcon from "@mui/icons-material/DownloadForOfflineRounded";
import { MdOutlinePlaylistAdd } from "react-icons/md";
import { ImCross, ImFileText2 } from "react-icons/im";
import { BsFileEarmarkPdf, BsFileEarmarkText } from "react-icons/bs";
import {
  AiFillFileText,
  AiOutlineFile,
  AiOutlineFileExcel,
  AiOutlineFileImage,
} from "react-icons/ai";
import { GrDocument } from "react-icons/gr";
import CellRendererPrimeReactDataTable from "../PrimeReactTableComponent/CellRendererPrimeReactDataTable";
import { Column } from "primereact/column";
import moment from "moment";
import {
  FaFile,
  FaFileAlt,
  FaFileImage,
  FaPlus,
  FaRegFileImage,
  FaRegFileWord,
  FaSave,
} from "react-icons/fa";
import { BiCheck } from "react-icons/bi";
import Loader from "../Loader/Loader";
import { IoWarningOutline } from "react-icons/io5";

function Documents(props) {
  const { vendorId, vendorData } = props;
  const loggedUserId = localStorage.getItem("resId");
  const [data, setData] = useState([{}]);
  const [visible, setVisible] = useState(false);
  const [headerData, setHeaderData] = useState([]);
  const [docId, setDocId] = useState("");
  const [selectedFile, setSelectedFile] = useState([]);
  const [uploadedSucessmessage, setUploadedSucessMessage] = useState(false);
  const [validationMessage, setValidationMessage] = useState(false);
  const [validationMessageempty, setValidationMessageEempty] = useState(false);
  const [showComponent, setShowComponent] = useState(false);

  const [loader, setLoader] = useState(false);
  const abortController = useRef(null);
  const handleAbort = () => {
    abortController.current && abortController.current.abort();
    setLoader(false);
  };

  let rows = 10;
  const baseUrl = environment.baseUrl;
  console.log(data);

  const getData = () => {
    axios
      .get(
        baseUrl +
          `/VendorMS/vendor/getDocumentDetails?vid=${vendorId}&object_type_id=15`
      )
      .then((res) => {
        const GetData = res.data;
        const Headerdata = [
          {
            file_name: "File Name",
            file_size: "File Size",
            file_type: "File Type",
            date_created: "Created On",
            created_by: "Created By",
            last_updated: "Updated On",
            Action: "Action",
          },
        ];
        setData(Headerdata.concat(GetData));
        // console.log(data)
        for (let i = 0; i < GetData.length; i++) {
          GetData[i]["file_name"] =
            GetData[i]["file_name"] == null
              ? ""
              : GetData[i]["file_name"].split(".")[0];
          GetData[i]["date_created"] =
            GetData[i]["date_created"] == null
              ? ""
              : moment(GetData[i]["date_created"]).format(
                  "DD-MMM-yyyy hh:mm:ss A"
                );
          GetData[i]["last_updated"] =
            GetData[i]["last_updated"] == null
              ? ""
              : moment(GetData[i]["last_updated"]).format(
                  "DD-MMM-yyyy hh:mm:ss A"
                );
        }
      })
      .catch((error) => {
        console.log("Error :" + error);
      });
  };
  useEffect(() => {
    getData();
    getDocId();
  }, []);

  const getDocId = () => {
    axios
      .get(
        baseUrl + `/VendorMS/vendor/getDocumentFolderID?object_id=${vendorId}`
      )
      .then((res) => {
        const data = res.data;
        console.log(data);
        setDocId(data);
      });
  };

  // const Upload = () => {
  //   const formData = new FormData();
  //   formData.append("file", selectedFile);
  //   setLoader(true);

  //   // Clear error and success messages
  //   setValidationMessage(false);
  //   setValidationMessageEempty(false);

  //   axios
  //     .post(
  //       baseUrl +
  //         `/VendorMS/vendor/updateDocumentDetails?docFolderId=${docId[0]?.id}&commitMessage=testing&loggedUserId=${loggedUserId}`,
  //       formData
  //     )
  //     .then((res) => {
  //       console.log(res);
  //       const Data = res.data;
  //       console.log(Data);
  //       if (Data.statusMessage == "may be same file exists at same location") {
  //         setValidationMessage(true);
  //         setValidationMessageEempty(false);
  //         window.scrollTo({ top: 0, behavior: "smooth" });
  //       } else {
  //         setValidationMessage(false);
  //         setUploadedSucessMessage(true);
  //         setTimeout(() => {
  //           setUploadedSucessMessage(false);
  //         }, 3000);
  //         getData();
  //         setLoader(false);
  //       }
  //       setLoader(false);
  //     })
  //     .catch((error) => {
  //       console.error("Error:", error);
  //       setLoader(false);
  //       if (error) {
  //         setValidationMessageEempty(true);
  //         setLoader(false);
  //         setValidationMessage(false);
  //         window.scrollTo({ top: 0, behavior: "smooth" });
  //       } else {
  //         setValidationMessageEempty(false);
  //       }

  //       // Handle errors here
  //     });
  // };

  const Upload = () => {
    const formData = new FormData();
    formData.append("file", selectedFile);

    // Clear success and error messages
    setUploadedSucessMessage(false);
    setValidationMessage(false);
    setValidationMessageEempty(false);

    if (!selectedFile) {
      // Show the error message if no file is selected
      setValidationMessageEempty(true);
      window.scrollTo({ top: 0, behavior: "smooth" });

      // Set a timer to hide the error message after 3 seconds (3000 milliseconds)
      setTimeout(() => {
        setValidationMessageEempty(false);
      }, 3000);

      return;
    }

    setLoader(true);

    axios
      .post(
        baseUrl +
          `/VendorMS/vendor/updateDocumentDetails?docFolderId=${docId[0]?.id}&commitMessage=testing&loggedUserId=${loggedUserId}`,
        formData
      )
      .then((res) => {
        console.log(res);
        const Data = res.data;
        console.log(Data);
        if (
          Data.statusMessage ===
          "may be the same file exists at the same location"
        ) {
          setValidationMessage(true);
          setLoader(false);
          window.scrollTo({ top: 0, behavior: "smooth" });

          // Set a timer to hide the error message after 3 seconds (3000 milliseconds)
          setTimeout(() => {
            setValidationMessage(false);
          }, 3000);
        } else {
          setValidationMessage(false);
          setUploadedSucessMessage(true);

          // Set a timer to hide the success message after 3 seconds (3000 milliseconds)
          setTimeout(() => {
            setUploadedSucessMessage(false);
          }, 3000);

          getData();
          setLoader(false);
        }
      })
      .catch((error) => {
        console.error("Error:", error);
        setLoader(false);
        if (error) {
          setValidationMessageEempty(true);
          setLoader(false);
          window.scrollTo({ top: 0, behavior: "smooth" });

          // Set a timer to hide the error message after 3 seconds (3000 milliseconds)
          setTimeout(() => {
            setValidationMessageEempty(false);
          }, 3000);
        }
      });
  };

  const onFileChangeHandler = (e) => {
    setSelectedFile(e.target.files[0]);
  };
  const Template = (data) => {
    return (
      <>
        <div align="center" data-toggle="tooltip" title={data.file_type}>
          {data.file_type == ".pdf" ? (
            <BsFileEarmarkPdf size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".xlsx" ? (
            <AiOutlineFileExcel size={"1.8em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".htm" ? (
            <GrDocument size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".txt" ? (
            <BsFileEarmarkText size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".jpeg" ? (
            <AiOutlineFileImage size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".docx" ? (
            <FaRegFileWord size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".Docx" ? (
            <AiOutlineFile size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".png" ? (
            <FaRegFileImage size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".html" ? (
            <AiOutlineFile size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : data.file_type == ".jpg" ? (
            <FaRegFileImage size={"1.5em"} style={{ color: " #2e88c5" }} />
          ) : (
            ""
          )}
        </div>
      </>
    );
  };

  const Action = (data) => {
    return (
      <div align="center" data-toggle="tooltip" title="Download Document">
        <DownloadForOfflineRoundedIcon
          style={{ color: "#86b558", cursor: "pointer" }}
          onClick={() => {
            handleDownload(data);
          }}
        />
      </div>
    );
  };

  const fName = (data) => {
    return (
      <div data-toggle="tooltip" title={data.file_name} className="ellipsis">
        {data.file_name}
      </div>
    );
  };

  const fileSize = (data) => {
    return (
      <div
        data-toggle="tooltip"
        className="ellipsis"
        title={data.file_size}
        style={{ fontStyle: "italic" }}>
        {data.file_size}
      </div>
    );
  };

  const dateCreated = (data) => {
    return (
      <div data-toggle="tooltip" className="ellipsis" title={data.date_created}>
        {data.date_created}
      </div>
    );
  };

  const createdBy = (data) => {
    return (
      <div data-toggle="tooltip" className="ellipsis" title={data.created_by}>
        {data.created_by}
      </div>
    );
  };

  const updatedOn = (data) => {
    return (
      <div data-toggle="tooltip" className="ellipsis" title={data.date_created}>
        {data.date_created}
      </div>
    );
  };
  const dynamicColumns = Object.keys(headerData)?.map((col, i) => {
    return (
      <Column
        sortable
        key={col}
        body={
          col == "file_type"
            ? Template
            : col == "file_name"
            ? fName
            : col == "file_size"
            ? fileSize
            : col == "date_created"
            ? dateCreated
            : col == "created_by"
            ? createdBy
            : col == "last_updated"
            ? updatedOn
            : col == "Action" && Action
        }
        field={col}
        header={headerData[col]}
      />
    );
  });

  useEffect(() => {
    data[0] && setHeaderData(JSON.parse(JSON.stringify(data[0])));
  }, [data]);
  //==============For Download===========================
  const handleDownload = (data, vendorDetails) => {
    console.log(data?.file_name + "in line 140...");
    const docUrl =
      baseUrl + `/VendorMS/vendor/downloadFile?documentId=${data?.id}`;
    axios({
      url: docUrl,
      method: "GET",
      responseType: "blob",
    }).then((response) => {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", data?.file_name + data?.file_type);
      document.body.appendChild(link);
      link.click();
    });
  };
  return (
    <div>
      <div className="col-md-12">
        {uploadedSucessmessage && (
          <div className="statusMsg success">
            {" "}
            <span className="errMsg">
              <BiCheck size="1.4em" /> &nbsp;{selectedFile?.name} uploaded
              successfully
            </span>
          </div>
        )}
        {validationMessage && (
          <div className="statusMsg error">
            {" "}
            <span className="errMsg">
              <IoWarningOutline size="1.4em" /> &nbsp;{selectedFile?.name}{" "}
              &nbsp; file(s) already exists in &nbsp;
              {vendorData[0]?.vendorName}
            </span>
          </div>
        )}
        {validationMessageempty && (
          <div className="statusMsg error">
            {" "}
            <span className="errMsg">
              <IoWarningOutline size="1.4em" /> &nbsp;Please select file(s) to
              upload
            </span>
          </div>
        )}
        {loader ? <Loader handleAbort={handleAbort} /> : ""}

        <div className="pageTitle">
          <div className="childOne"></div>
          <div className="childTwo">
            <h2>Documents</h2>
          </div>
          <div className="childThree"></div>
        </div>
      </div>
      {showComponent && (
        <div className="group mb-3 customCard">
          <h2>Document Repository</h2>
        </div>
      )}
      {/* <FlatPrimeReactTable data={data} rows={rows} /> */}
      <CellRendererPrimeReactDataTable
        data={data}
        rows={rows}
        dynamicColumns={dynamicColumns}
        headerData={headerData}
        setHeaderData={setHeaderData}
      />
      <div className="col-md-12 col-sm-12 col-xs-12 my-2 btn-container center">
        <button
          type="button"
          className="btn btn-primary"
          onClick={() => {
            setVisible(true);
          }}
          style={{ cursor: visible == true ? "not-allowed" : "" }}>
          {" "}
          <FaPlus />
          Add
        </button>
      </div>
      {visible && (
        <div>
          <div>
            <div className="form-group row">
              <span className="col-4 ml-1">
                <strong>Note:</strong>You can select 10 files at max.
              </span>
            </div>
            <div className="col-9 p-0">
              <table className="table table-bordered table-striped" role="grid">
                <tbody>
                  <tr>
                    <th>Browse Document</th>
                  </tr>
                  <tr>
                    <td>
                      <input
                        type="file"
                        name="docId"
                        id="docId"
                        multiple
                        accept=".jpg,.jpeg,.xlsx,.pdf,.docx,.txt,.doc,.csv,.html,.png"
                        onChange={onFileChangeHandler}
                      />
                      <label className="documenttypes col-md-12">
                        <p className="error-text">
                          Supported file types
                          .pdf,.doc,.csv,.txt,.xlsx,.html,.png{" "}
                        </p>
                      </label>
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>

          <div className=" form-group col-md-12 col-sm-12 col-xs-12 btn-container center my-3 mb-2">
            <button className="btn btn-primary" type="submit" onClick={Upload}>
              <FaSave />
              Save
            </button>
            <button
              className="btn btn-secondary"
              onClick={() => {
                setVisible(false);
                setValidationMessage(false);
                setValidationMessageEempty(false);
              }}>
              <ImCross fontSize={"11px"} /> Cancel
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
export default Documents;
